.starter {
  font-size: 18px;
}

.displayData {
  width: 250px;
  border-radius: 25px;
  background-color: lightgray;
  border: 1px solid black;
  box-shadow: 5px 5px 5px lightgray;
  margin: 0 5px 5px;
  padding: 5px 3px;
}

.fields_wrapper {
  display: flex;
  justify-content: space-between;
}

.displayWrapper {
  display: flex;
  flex-wrap: wrap;
}

textarea {
  flex-shrink: 0;
  width: 300px;
}

.btn {
  margin: 5px;
  border-radius: 10px;
  background-color: lightcoral;
  font-size: 20px;
  color: white;
  padding: 5px 15px;
  border: 2px solid black;
  transition: transform 0.5s;
}

.btn:hover {
  box-shadow: 5px 5px 10px silver;
}

.btn:active {
  transform: scale(1.1);
}
